const embedPrefix = window._heyformBlock.embedUrlPrefix

export const HEYFORM_SHARING_URL_REGEX = new RegExp(
  `^${embedPrefix}/([a-z0-9A-Z]{8,})$`
)

export function getEmbedUrl(formId: string) {
  return `${embedPrefix}/${formId}`
}

export function getEmbedScriptUrl(formId: string) {
  return `${embedPrefix}/${formId}/e.js`
}
