import React from 'react'
import { getEmbedUrl } from '../utils'

interface BlockCodeProps {
  formId: string
  width: string
  height: string
}

export const BlockCode = ({ formId, width, height }: BlockCodeProps) => {
  const embedUrl = getEmbedUrl(formId)

  return (
    <div className="heyform-block">
      <iframe
        src={embedUrl}
        style={{
          width: width || '100%',
          height: height || '100%',
          border: 'none',
          overflow: 'hidden'
        }}
      />
    </div>
  )
}
