import React from 'react'
import { getEmbedUrl } from '../utils'

interface EmbedProps {
  formId: string
  width: string
  height: string
}

export const EditorPreview = ({ formId, width, height }: EmbedProps) => {
  const embedUrl = getEmbedUrl(formId)

  return (
    <figure className="wp-block-embed-heyform wp-block-embed">
      <div className="wp-block-embed__wrapper">
        <iframe
          src={embedUrl}
          style={{
            width: width || '100%',
            height: height || '100%',
            border: 'none',
            overflow: 'hidden'
          }}
        />
      </div>
    </figure>
  )
}
