import { isValid } from '@puckjs/utils/lib/helper'
import React from 'react'
import { BlockCode } from './components/BlockCode'
import { EditorPreview } from './components/EditorPreview'
import { EmbedForm } from './components/EmbedForm'
import { LogoIcon } from './components/LogoIcon'
import { HEYFORM_SHARING_URL_REGEX } from './utils'
import './block.scss'

function registerBlock() {
  const { blocks, components, editor, i18n } = window.wp
  const { TextControl } = components
  const { InspectorAdvancedControls } = editor
  const { registerBlockType } = blocks
  const { __ } = i18n

  registerBlockType('heyform/heyform-block', {
    title: __('HeyForm', 'heyform'),
    category: 'common',
    icon: LogoIcon,
    description: __(
      'Serverless Online Form Builder & Data Management Platform',
      'heyform'
    ),
    attributes: {
      formId: {
        type: 'string'
      },
      width: {
        type: 'string',
        default: '100%'
      },
      height: {
        type: 'string',
        default: '600px'
      }
    },

    edit({ attributes: { formId, width, height }, setAttributes }: any) {
      function handleChange(event: any) {
        const value = event.target.elements['formId'].value
        const matches = value.match(HEYFORM_SHARING_URL_REGEX)

        if (matches) {
          const formId = matches[1]

          setAttributes({
            formId
          })
        }

        event.preventDefault()
      }

      return (
        <div className="components-heyform">
          {isValid(formId) ? (
            <EditorPreview formId={formId} width={width} height={height} />
          ) : (
            <EmbedForm onChange={handleChange} />
          )}
          <InspectorAdvancedControls>
            <TextControl
              label={__('Form Width', 'heyform')}
              value={width}
              onChange={(width: string) => setAttributes({ width })}
            />
            <TextControl
              label={__('Form Height', 'heyform')}
              value={height}
              onChange={(height: string) => setAttributes({ height })}
            />
          </InspectorAdvancedControls>
        </div>
      )
    },

    save({ attributes: { formId, width, height } }: any) {
      return <BlockCode formId={formId} width={width} height={height} />
    }
  })
}

registerBlock()
